import React, {Component} from 'react'

class ArrowLink extends Component {
  constructor(props){
    super(props);
    this.state = {}
  }
  render(){
    return (
      <div className={"arrowCont"}>
        <div className={"arrowContent"}> {this.props.content} </div>
        <div className={"arrowArrow"}> <b> {">"} </b> </div>
      </div>
    )
  }
}

export default ArrowLink
