import React, {Component} from 'react'

import trashcan from '../media/Trashcan.png'

class ForeningItem extends Component {
  constructor(props){
    super(props);
    this.state = {
      expand: false,
      newNotification: true
    }
  }
  expandFunc = (expandState) => {
    this.setState({expand: !expandState})
  }
  render(){
    let expandInfo = (this.state.expand ? "visible" : "invisible")
    let newNotification = (this.state.newNotification ? "visible" : "invisible")
    return (
      <div className={"fItemCont"}>
        <div className={"fItemAlert " + newNotification}><b>Ny!</b></div>
        <div className={"fItemTitles"}>
          <div className={"fItemMainTitle"}> <strong> {this.props.forening.shortname} </strong> </div>
          <div className={"fItemSubtitles"}>
            <div> {this.props.forening.subcategory}, {this.props.forening.location.city}</div>
          </div>
        </div>
        <div className={"fItemDelete"} onClick={() => this.props.removeForening(this.props.index)}> <img className={"trashImage"} src={trashcan} alt={"trhc"}/> </div>
        <button className={"fItemModalBtn"} onClick={() => this.expandFunc(this.state.expand)}><b>˅</b></button>
        <div className={"fItemExpand " + expandInfo}>
          <p> <b>Points:</b> {this.props.forening.points}</p>
          <p> <b>Supporters:</b> {this.props.forening.supporters}</p>
          <p> <b>Homepage:</b> {this.props.forening.homepage}</p>
          <p> <b>Category:</b> {this.props.forening.category}</p>
        </div>
      </div>
    )
  }
}

export default ForeningItem

