import React, {Component} from 'react'

import Main from './main.js'
import Banner from './banner.js'
import SideItem from './sideItem.js'
import '../css/all.css'

import sideItems from '../json/sideItems.json'

class All extends Component {
  constructor(props){
    super(props);
    this.state = {
      database: {},
    }
  }
  renderSideItems = (itemArray) => {
    return itemArray.map((item, i) => {
      return <SideItem key={"all" + item.mainTitle + i} fullItem={item}/>
    })
  }
  removeForening = (index) => {
    let currentDatabase = this.state.database
    currentDatabase.splice(index, 1);
    this.setState({database: currentDatabase})
  }
  addForening = (newForening) => {
    let currentDatabase = this.state.database
    currentDatabase.unshift(newForening);
    this.setState({database: currentDatabase})
  }
  componentDidMount(){
    fetch("http://localhost:3000/foreningar")
    .then( res => res.json())
    .then((result) => {
      this.setState({database: result})
    })
  }
  render(){
    console.log(this.state.database)
    return (
      <div className={"allCont"}>
        <Banner/>
        <Main
          database={this.state.database}
          removeForening={this.removeForening}
          addForening={this.addForening}
        />
        <div className={"allSideItemCont"}>
          {this.renderSideItems(sideItems)}
        </div>
      </div>
    )
  }
}

export default All
