import React, {Component} from 'react'

const initialStateVariables = ["shortname","subcategory","homepage","city","zipcode","address"]

class AddForeningModal extends Component {
  constructor(props){
    super(props);
    this.state = {
      shortname: "",
      subcategory: "",
      homepage: "",
      city: "",
      zipcode: "",
      address: ""
    }
  }
  resetState = () => {
    let initialState = {};
    initialStateVariables.forEach((variable) => {
      initialState[variable] = ""
    })
    this.setState({...initialState})
  }
  updateValue = (event, path) => {
    this.setState({[path]: event.target.value})
  }
  fuseForening = (stateitems) => {
    let newForeningObj = {
      shortname: stateitems.shortname,
      subcategory: stateitems.subcategory,
      homepage: stateitems.homepage,
      location: {
        city: stateitems.city,
        zipcode: stateitems.zipcode,
        address: stateitems.address
      },
      points: 0,
      supporters: 0,
      category: "Idrottsförening",
    }
    return newForeningObj
  }
  onCompletion = (newForening) => {
    this.props.addForening(newForening)
    this.props.displayModalFunc()
    this.resetState()
  }
  render(){
    return (
      <div className={"addModalCont " + this.props.visible}>
        <div className={"addFormTitle"}> Lägg till Förening </div>
        <form className={"addModalForm"}>
          <input type={"text"} onChange={(event) => this.updateValue(event, "shortname")} placeholder={"Föreningsnamn"} value={this.state.shortname}/>
          <input type={"text"} onChange={(event) => this.updateValue(event, "subcategory")} placeholder={"Sport"} value={this.state.subcategory}/>
          <input type={"text"} onChange={(event) => this.updateValue(event, "zipcode")} placeholder={"Postnummer"} value={this.state.zipcode}/>
          <input type={"text"} onChange={(event) => this.updateValue(event, "city")} placeholder={"Ort"} value={this.state.city}/>
          <input type={"text"} onChange={(event) => this.updateValue(event, "address")} placeholder={"Address"} value={this.state.address}/>
          <input type={"text"} onChange={(event) => this.updateValue(event, "homepage")} placeholder={"Url till hemsida"} value={this.state.homepage}/>
          <button type={"button"} onClick={() => this.onCompletion(this.fuseForening(this.state))}> Complete </button>
          <button type={"button"} onClick={this.props.displayModalFunc}> Cancel </button>
        </form>
      </div>
    )
  }
}

export default AddForeningModal


