import React, {Component} from 'react'

import ForeningItem from './foreningItem'
import AddForeningModal from './addForeningModal.js'

import '../css/foreningar.css'

class MinaForeningar extends Component {
  constructor(props){
    super(props);
    this.state = {
      displayCount: 3,
      displayModal: false,
      foreningFocus: {
        state: false,
        index: 0
      }
    }
  }
  renderForeningar = (foreningData, displayCount) => {
    return foreningData.map((forening, i) => {
      if(i >= 3){
        return null
      }
      else {
        return (
          <ForeningItem
            key={"foreningItem" + i}
            forening={forening}
            index={i}
            removeForening={this.props.removeForening}
            addForening={this.props.addForening}
          />
        )
      }
    })
  }
  renderFocus = (foreningData, index) => {
    return <ForeningItem
      forening={foreningData[index]}
      index={index}
      removeForening={this.props.removeForening}
      addForening={this.props.addForening}
    />
  }
  displayModalFunc = (currentDisplayState) => {
    this.setState({displayModal: !currentDisplayState})
  }
  render(){
    let modalVisibility = (this.state.displayModal ? "visible" : "invisible")
    let restVisiblity = (!this.state.displayModal ? "visible" : "invisible")
    return (
      <div className={"mForeningarCont"}>
      <AddForeningModal addForening={this.props.addForening} displayModalFunc={() => this.displayModalFunc(this.state.displayModal)} visible={modalVisibility}/>
        <div className={"mForenignarDisplay " + restVisiblity}>
          <div className={"mForeningarTitles"}>
            <div className={"mForingarTitleMain"}> <strong>Mina Föreningar</strong> </div>
            <div className={"mForingarTitleContent"}> Du kan stödja upp till 3 föreningar samtidigt. Poängen fördelas jämt över föreningarna. </div>
          </div>
          <div className={"mForeningarList"}>
            {this.props.database && this.props.database.length > 0 ? this.renderForeningar(this.props.database, this.state.displayCount) : null}
          </div>
        </div>
        <div className={"addForeningCont " + restVisiblity}>
          <button className={"addForeningBtn"} onClick={() => this.displayModalFunc(this.state.displayModal)}> Lägg till en förening </button>
        </div>
      </div>
    )
  }
}

export default MinaForeningar
