import React, {Component} from 'react'

import ArrowLink from './arrowLink.js'

import '../css/sideItem.css'

class SideItem extends Component {
  constructor(props){
    super(props);
    this.state = {}
  }
  renderArrowLinks = (fullItem) => {
    let arrowRenders = [];
    for(let i=0; i < fullItem.arrowCount; i++){
      arrowRenders[i] = <ArrowLink key={"side" + fullItem.mainTitle + i} content={fullItem.arrowContent[i]}/>
    }
    return arrowRenders
  }
  render(){
    let subTitle = this.props.fullItem.subTitle.toUpperCase()
    return (
      <div className={"sideItemCont"}>
        <div className={"sideItemMainCont"}>
          <div className={"sideItemSubtitle"}> {subTitle} </div>
          <div className={"sideItemTitle"}> <strong> {this.props.fullItem.mainTitle} </strong> </div>
          <div className={"sideItemContent"}> {this.props.fullItem.mainContent} </div>
        </div>
        <div className={"sideItemArrows"}><b>{this.renderArrowLinks(this.props.fullItem)}</b></div>
      </div>
    )
  }
}

export default SideItem
