import React, {Component} from 'react'

import MinaForeningar from './minaForeningar.js'

import '../css/main.css'
import mainTextContent from '../json/main.json'

class Main extends Component {
  constructor(props){
    super(props);
    this.state = {}
  }
  render(){
    return (
      <div className={"mainContainer"}>
        <div className={"mainLeft"}>
          <div className={"mainTitle"}><b>{mainTextContent.mainTitle}</b></div>
          <div className={"mainContent"}>{mainTextContent.mainContent}</div>
        </div>
        <div className={"mainRight"}>
          <MinaForeningar
            database={this.props.database}
            removeForening={this.props.removeForening}
            addForening={this.props.addForening}
          />
        </div>
      </div>
    )
  }
}

export default Main
